import { useNavigate, useParams } from "react-router-dom";
import { useState, useEffect, useRef } from "react";
import axios from "axios";

const ServicesByCentreLayer = () => {
  const { id } = useParams();
  const navigate = useNavigate();

  const [services, setServices] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedService, setSelectedService] = useState(null);

  // Pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const recordsPerPage = 15;

  // Create a ref for the modal
  const modalRef = useRef(null);

  useEffect(() => {
    const fetchServices = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centreService/${id}`
        );
        setServices(response.data || []);
      } catch (error) {
        console.error("Error fetching services:", error);
      }
    };

    fetchServices();
  }, [id]);

  // Handle clicks outside the modal
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (modalRef.current && !modalRef.current.contains(event.target) && showModal) {
        setShowModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showModal]);

  const handleViewDetails = (service) => {
    setSelectedService(service);
    setShowModal(true);
  };

  // --- Pagination Logic ---
  const indexOfLastRecord = currentPage * recordsPerPage;
  const indexOfFirstRecord = indexOfLastRecord - recordsPerPage;
  const currentRecords = services.slice(indexOfFirstRecord, indexOfLastRecord);
  const totalPages = Math.ceil(services.length / recordsPerPage);

    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              showModal
            ) {
              showModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ðŸ”‘ Scroll lock logic
          if (showModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [showModal]);

  return (
    <div className="dashboard-body__content">
      {/* Service List */}
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <h4 className="mb-3">Available Services</h4>
          </div>

          {services.length > 0 ? (
            <>
              <div className="table-responsive">
                <table className="table text-body">
                  <thead>
                    <tr>
                      <th>SN.</th>
                      <th>Category</th>
                      <th>Service Name</th>
                      <th>Price</th>
                      <th>Doctors</th>
                      <th>Shift Times</th>
                      <th>Description</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentRecords.map((service, index) => (
                      <tr key={service._id}>
                        <td>{indexOfFirstRecord + index + 1}</td>
                        <td>{service.serviceCategory}</td>
                        <td>{service.serviceName}</td>
                        <td style={{ textAlign: "left" }}>â‚¹{service.totalPrice}</td>
                        <td style={{ textAlign: "left" }}>
                          {service?.doctors.slice(0, 3).map((doc, i) => (
                            <div key={i}>
                              {doc.name}
                              {i === 2 && service.doctors.length > 3 && (
                                <span
                                  style={{
                                    fontSize: "12px",
                                    color: "blue",
                                    cursor: "pointer",
                                    marginLeft: "8px",
                                    textDecoration: "underline",
                                  }}
                                  onClick={() => handleViewDetails(service)}
                                >
                                  View More
                                </span>
                              )}
                            </div>
                          ))}
                        </td>
                        <td style={{ textAlign: "left" }}>
                          {service.slots.slice(0, 3).map((slot, i) => (
                            <div key={i}>
                              {slot.day.slice(0, 3)}: {slot.from} - {slot.to}
                            </div>
                          ))}
                          {service.slots.length > 3 && (
                            <span
                              style={{
                                fontSize: "12px",
                                color: "blue",
                                cursor: "pointer",
                                marginLeft: "8px",
                                textDecoration: "underline",
                              }}
                              onClick={() => handleViewDetails(service)}
                            >
                              View More
                            </span>
                          )}
                        </td>

                        <td
                          style={{
                            maxWidth: "300px",
                            overflow: "hidden",
                            whiteSpace: "nowrap",
                            textOverflow: "ellipsis",
                          }}
                        >
                          {service.description}
                        </td>
                        <td>
                          <button
                            onClick={() => {
                              setSelectedService(service);
                              setShowModal(true);
                            }}
                            style={{
                              backgroundColor: "#007bff",
                              color: "white",
                              border: "none",
                              borderRadius: "4px",
                              padding: "5px 10px",
                              marginRight: "5px",
                            }}
                          >
                            View
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {/* Pagination Controls */}
              <div style={{ display: "flex", justifyContent: "center", marginTop: "15px" }}>
                <button
                  disabled={currentPage === 1}
                  onClick={() => setCurrentPage((prev) => prev - 1)}
                  style={{ marginRight: "10px" }}
                >
                  Previous
                </button>
                <span>
                  Page {currentPage} of {totalPages}
                </span>
                <button
                  disabled={currentPage === totalPages}
                  onClick={() => setCurrentPage((prev) => prev + 1)}
                  style={{ marginLeft: "10px" }}
                >
                  Next
                </button>
              </div>
            </>
          ) : (
            <p className="text-muted">No services added yet.</p>
          )}
        </div>
      </div>

      {/* Modal code stays the same */}
      {showModal && selectedService && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowModal(false)}
        >
          <div
            ref={modalRef}
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "700px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              overflow: "hidden",
                maxHeight: "90vh",        // âœ… allows modal to scroll inside viewport
    overflowY: "auto",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Modal Header */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position:"sticky",
                top:0,
                zIndex:10,
              }}
            >
              {selectedService.serviceName}
              <button
                onClick={() => setShowModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                  overflowY: "auto",
                }}
              >
                &times;
              </button>
            </div>

            {/* Modal Body */}
            <div
              style={{
                display: "flex",
                padding: "20px",
                flexWrap: "wrap",
                gap: "20px",
              }}
            >
              <div style={{ flex: "1", minWidth: "250px" }}>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Category</strong>
                  </p>
                  <p>{selectedService.serviceCategory}</p>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Price</strong>
                  </p>
                  <p>â‚¹{selectedService.totalPrice}</p>
                </div>
              </div>

              <div style={{ flex: "1", minWidth: "250px" }}>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Doctors</strong>
                  </p>
                  <ul>
                    {selectedService.doctors.map((doc, i) => (
                      <li key={i}>{doc?.name}</li>
                    ))}
                  </ul>
                </div>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Shift Times</strong>
                  </p>
                  <ul>
                    {selectedService.slots.map((slot, i) => (
                      <li key={i}>
                        {slot.day}: {slot.from} - {slot.to}
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            </div>

            <div style={{ padding: "0 20px 20px 20px" }}>
              <div style={{ marginTop: "10px" }}>
                <p>
                  <strong style={{ color: "black" }}>Description</strong>
                </p>
                <p>{selectedService.description}</p>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ServicesByCentreLayer;
